import React, { useEffect, useState, useCallback } from "react"
import { Row, Col, List } from "antd"
import { withRouter } from "react-router-dom"
import styled from "styled-components"

import { Section, Heading, Layout } from "components"
import FilteringPanel from "./FilteringPanel"
import TutorCard from "components/common/TutorCard"
import { client, newClient } from "helpers"
import { queryFetchSubjects, queryFetchLevels } from "queries/data"
import SelectInput from "components/forms/SelectInput"
import { useMutation, useQuery } from "react-apollo"
import { MUTATE_SEARCH_TUTORS } from "queries/user"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { country, AppContext } from "App"
import { countryMap } from "helpers/countryMapping"

const TutorList = styled(List)`
	.ant-row {
		display: flex;
		flex-wrap: wrap;
		.ant-col {
			margin-bottom: 0;
			.ant-list-item {
				height: 350px;
			}
		}
	}
`

const programmeClient = newClient("programme")

function Marketplace({ location, history, i18n }) {
	const initialPriceRange = (country && countryMap[country].pricePerHourRange.range) || []

	const initialValues = {
		keywords: "",
		curriculums: 0,
		subjects: 0,
		alphabet: 0,
		recently: 0,
		location: "",
		price_range: { startPrice: initialPriceRange[0], endPrice: initialPriceRange[1] },
	}

	const { data: levels = {} } = useQuery(queryFetchLevels, { client: programmeClient, variables: { country } })
	const { data: subj = {} } = useQuery(queryFetchSubjects, { client: programmeClient, variables: { country } })
	const [users, setUsers] = useState([])
	const [formValues, setFormValues] = useState(initialValues)

	const { onMarketplace } = React.useContext(AppContext)

	const params = new URLSearchParams(location.search)
	const subjectParams = params.get("subjects")
	const keywordParams = params.get("keywords")

	const fromNavbar = JSON.parse(localStorage.getItem("fromNavbar")) || {}

	const [searchUsers, { loading }] = useMutation(MUTATE_SEARCH_TUTORS, {
		client: client.profileClient,
		variables: { country: country || "sg" },
		onCompleted: (data) => {
			data = data.findTutors || []
			setUsers(data)
		},
		onError(err) {
			console.log("searchUsers", err)
		},
	})

	const levelsData = levels.getProgrammeLevelList || []
	const subjectsData = subj.getProgrammeSubjectList || []
	const levelsOptions = levelsData.map((item) => ({ value: Number(item.pvid), label: item.name }))
	const subjOptions = subjectsData.map((item) => ({ value: Number(item.pvid), label: item.name }))
	const subjectLabel = (subjOptions.find((item) => item.value === formValues.subjects) || {}).label || ""
	const curriculumLabel = (levelsOptions.find((item) => item.value === formValues.curriculums) || {}).label || ""

	const handleSortUsers = (value) => {
		const recently = value === "recently" ? 1 : 0
		const alphabet = value === "alphabet" ? 1 : 0
		const values = { ...formValues, country, recently, alphabet }
		setFormValues((prev) => ({ ...prev, country, recently, alphabet }))
		searchUsers({ variables: values })
	}

	const handleSearchFilter = useCallback(
		(name) => (e) => {
			const { price_range } = formValues
			setFormValues((prev) => ({ ...prev, [name]: e.target.value }))

			const values = {
				...formValues,
				[name]: e.target.value,
				price_range: { startPrice: price_range.startPrice, endPrice: price_range.endPrice },
			}

			if (window.location.search !== "") history.push(`/${country}/browse/tutors`)
			searchUsers({ variables: values })
		},
		[formValues, history, searchUsers, setFormValues]
	)

	const handleChangeSlider = useCallback(
		(value) => {
			setFormValues((prev) => ({ ...prev, price_range: { startPrice: value[0], endPrice: value[1] } }))
			const values = {
				...formValues,
				price_range: { startPrice: value[0], endPrice: value[1] },
			}

			if (window.location.search !== "") history.push(`/${country}/browse/tutors`)
			searchUsers({ variables: values })
		},
		[formValues, history, searchUsers, setFormValues]
	)

	const handleClickTutor = (item) => {
		onMarketplace.setValueFromMarketplace({
			keywords: formValues.keywords,
			subjects: subjectLabel,
			curriculums: curriculumLabel,
		})
		window.open(`/${country}/${item.token}--${item.pvid}--${item.firstName}--${item.lastName}`)
	}

	useEffect(() => {
		if (subjectParams) {
			searchUsers({ variables: { ...formValues, subjects: Number(subjectParams) } })
		} else if (keywordParams) {
			searchUsers({ variables: { ...formValues, keywords: keywordParams } })
		} else searchUsers({ variables: formValues })
	}, [formValues, searchUsers, subjectParams, keywordParams])

	return (
		<Layout>
			<Section>
				<Row gutter={48}>
					<Col lg={6}>
						<FilteringPanel
							options={{ levelsOptions, subjOptions }}
							onUsers={{ fromNavbar, handleSearchFilter, handleChangeSlider }}
							onFormValues={{ formValues, setFormValues }}
							params={{ subjectParams, keywordParams }}
							location={{ location, history }}
						/>
					</Col>
					<Col lg={18}>
						<Section noPadding className="mb2em smooth-scroll">
							<Row type="flex" justify="space-between" align="middle">
								<Col lg={12}>
									<Heading
										content={<Trans>Searches</Trans>}
										subheader={
											<Trans>
												<span>
													Showing&nbsp;
													<strong>{users.length} tutors</strong>
												</span>
											</Trans>
										}
										level={4}
									/>
								</Col>
								<Col lg={12} xs={12} style={{ textAlign: "right" }}>
									<Row gutter={16} type="flex" justify="end">
										<Col lg={12}>
											<SelectInput
												independent
												name="sort"
												defaultvalue={<Trans>Sort by...</Trans>}
												options={[
													{ value: "Sort by...", label: <Trans>Sort by...</Trans> },
													{ value: "recently", label: <Trans>Recently added</Trans> },
													{ value: "alphabet", label: <Trans>Alphabet</Trans> },
												]}
												onChange={handleSortUsers}
												placeholder={i18n._(t`Sort by...`)}
											/>
										</Col>
									</Row>
								</Col>
							</Row>
						</Section>
						<Section noPadding>
							<TutorList
								dataSource={users}
								grid={{ gutter: 16, xs: 2, lg: 4 }}
								loading={loading}
								pagination={{ onChange: () => window.scrollTo({ top: 0, behavior: "smooth" }), pageSize: 12 }}
								renderItem={(item) => {
									return (
										<List.Item key={item.pvid}>
											<TutorCard {...item} onClick={() => handleClickTutor(item)} />
										</List.Item>
									)
								}}
							/>
						</Section>
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}

export default withI18n()(withRouter(Marketplace))
